@import "https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400&display=swap";
*, :before, :after {
  box-sizing: inherit;
  color: #fff;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
  font-weight: 300;
}

html {
  scroll-behavior: smooth;
  box-sizing: border-box;
  font-size: 62.5%;
}

@media only screen and (max-width: 37.5em) {
  html {
    font-size: 50%;
  }
}

body {
  height: 100vh;
  background: #d3cce3;
  background: -webkit-linear-gradient(to top, #d3cce3, #e9e4f0);
  background: linear-gradient(to top, #d3cce3, #e9e4f0);
  justify-content: center;
  align-items: center;
  padding: 3rem;
  display: flex;
}

.container {
  max-width: 50rem;
  background-color: #444;
