  justify-content: space-around;
  align-items: flex-end;
  padding: 1.5rem;
  display: flex;
}

.display__previous {
  color: #b3b3b3;
  font-size: 3rem;
  font-weight: 200;
}

.display__current {
  font-size: 6rem;
  font-weight: 200;
}

button {
  color: inherit;
  cursor: pointer;
  background-color: gray;
  border: none;
  border-radius: 100px;
